import React from 'react';
import { Modal, ModalPublicProps } from 'components/modal';
import { useToggle } from 'hooks/useToggle';
import { HeaderModalContent } from 'templatesReact/miniSiteCommon/headerModal/HeaderModalContent';


const HeaderModal = (props: ModalPublicProps) => {
  const {
    className,
    isOpen,
    onClose,
    onOverlay,
    onEscape,
  } = props;

  const [triggerClosing, onTriggerClosing] = useToggle(false);

  return (
    <Modal
      className={className}
      title={'Premium feature'}
      widthSize='xs'
      isOpen={isOpen}
      onClose={onClose}
      onOverlay={onOverlay}
      onEscape={onEscape}
      triggerClosing={triggerClosing}
      onTriggerClosing={onTriggerClosing}
    >
      <HeaderModalContent onClose={onTriggerClosing}/>
    </Modal>
  );
};

export { HeaderModal };