import s from './styleProvider';
import React, { FC, ReactNode, MouseEvent, useState, useRef, useEffect, useCallback } from 'react';
import { Portal } from 'components/Portal/Portal';

export type ModalPublicProps = {
  className?: string;
  isOpen: boolean;
  onClose?: () => void;
  onOverlay?: () => void;
  onEscape?: () => void;
}

type ModalFullProps = ModalPublicProps & {
  title?: string;
  widthSize?: 'xs' | 's' | 'm' | 'l' | 'xl';
  children: ReactNode;
  triggerClosing?: boolean;
  onTriggerClosing?: () => void;
  triggerClosingCallback?: () => void;
  footerAdditionalContent?: ReactNode[];
  isLoading?: boolean
}

const ANIMATION_DELAY_MS = 200;

const Modal:FC<ModalFullProps> = props => {
  const {
    className,
    title,
    widthSize = 'm',
    children,
    isOpen,
    onClose,
    onOverlay,
    onEscape,
    triggerClosing,
    onTriggerClosing,
    triggerClosingCallback,
    isLoading,
  } = props;

  const [modalOpened, setModalOpened] = useState<boolean>(false);
  const [isClosing, setIsClosing] = useState<boolean>(false);
  const timerRef = useRef<NodeJS.Timeout | null>(null);

  const closeHandler = useCallback((callback?: () => void) => {
    if (!callback) {
      return;
    }
    setIsClosing(true);
    timerRef.current = setTimeout(() => {
      callback();
      setIsClosing(false);
      setModalOpened(false);
    }, ANIMATION_DELAY_MS);
  }, []);

  const onClickContent = (e: MouseEvent) => {
    e.stopPropagation();
  };

  useEffect(() => {
    const onKeyDown = (e: KeyboardEvent) => {
      if (onEscape && e.key === 'Escape') {
        closeHandler(onEscape);
      }
    };

    if (isOpen) {
      //window.addEventListener('keydown', onKeyDown);
      setModalOpened(true);
    }

    return () => {
      timerRef.current && clearTimeout(timerRef.current);
      //window.removeEventListener('keydown', onKeyDown);
    };
  }, [onEscape, closeHandler, isOpen]);

  useEffect(() => {
    if (triggerClosing && onTriggerClosing) {
      closeHandler(triggerClosingCallback ? triggerClosingCallback : onClose);
      onTriggerClosing();
    }
  }, [triggerClosing, closeHandler, onTriggerClosing, triggerClosingCallback, onClose]);

  if (!isOpen) return null;

  return (
    <Portal>
      <div
        className={`${s.modal} ${modalOpened && s.opened} ${isClosing && s.isClosing} ${className}`}
      >
        <div className={s.overlay} onClick={() => closeHandler(onOverlay)}>
          <div className={`${s.wrapper} ${s[widthSize]}`} onClick={onClickContent}>
            {title && <div className={s.title}>
              {title}
            </div>}
            <div className={`${s.body} ${title && s.body_title}`}>
              {isLoading
                ? <div>loading</div>
                : children
              }
            </div>
          </div>
        </div>
      </div>
    </Portal>
  );
};

export { Modal };
