import React from 'react';
import { FC, useState, ReactNode, useCallback } from 'react';
import { MiniSiteContext } from './Context';
import {
  MiniSiteDataT,
  ProductItemT,
  PhotoItemT,
  ReviewItemT,
  MemberItemT,
} from './types';


export interface MenuProviderProps {
  children: ReactNode;
}

const MiniSiteProvider: FC<MenuProviderProps> = ({ children }) => {
  const [data, setData] = useState<MiniSiteDataT>({} as MiniSiteDataT);

  const editImages = useCallback((
    imageUrl: string,
    logoUrl: string,
  ) => {
    setData({
      ...data,
      imageUrl,
      logoUrl,
    });
  }, [setData, data]);

  // const editAboutImage = useCallback((aboutImage: string) => {
  //   setData({ ...data, aboutImage });
  // }, [setData, data]);

  // const editAboutText = useCallback((aboutText: string) => {
  //   setData({ ...data, aboutText });
  // }, [setData, data]);

  // const editDetails = useCallback((
  //   detailsCreationDate: string,
  //   detailsFullName: string,
  //   detailsIndustry: string,
  //   detailsSiret: string,
  //   detailsAddress: string,
  //   detailsPhone: string,
  // ) => {
  //   setData({
  //     ...data,
  //     detailsCreationDate,
  //     detailsFullName,
  //     detailsIndustry,
  //     detailsSiret,
  //     detailsAddress,
  //     detailsPhone,
  //   });
  // }, [setData, data]);

  const addProductItem = useCallback((product: ProductItemT) => {
    setData({ ...data, products: [...data.products, product] });
  }, [setData, data]);

  const editProductItem = useCallback((id: string, updatedProduct: ProductItemT) => {
    const products = data.products.map(item => item.id === id ? updatedProduct : item);
    setData({ ...data, products });
  }, [setData, data]);

  const removeProductItem = useCallback((id: string)=> {
    const products = data.products.filter(item => item.id !== id);
    setData({ ...data, products });
  }, [setData, data]);



  const addPhotoItem = useCallback((photo: PhotoItemT) => {
    setData({ ...data, photos: [...data.photos, photo] });
  }, [setData, data]);

  const editPhotoItem = useCallback((id: string, updatedPhoto: PhotoItemT) => {
    const photos = data.photos.map(item => item.id === id ? updatedPhoto : item);
    setData({ ...data, photos });
  }, [setData, data]);

  const removePhotoItem = useCallback((id: string)=> {
    const photos = data.photos.filter(item => item.id !== id);
    setData({ ...data, photos });
  }, [setData, data]);



  const addReviewItem = useCallback((review: ReviewItemT) => {
    setData({ ...data, reviews: [...data.reviews, review] });
  }, [setData, data]);

  const editReviewItem = useCallback((id: string, updatedReview: ReviewItemT) => {
    const reviews = data.reviews.map(item => item.id === id ? updatedReview : item);
    setData({ ...data, reviews });
  }, [setData, data]);

  const removeReviewItem = useCallback((id: string)=> {
    const reviews = data.reviews.filter(item => item.id !== id);
    setData({ ...data, reviews });
  }, [setData, data]);



  const addMemberItem = useCallback((member: MemberItemT) => {
    setData({ ...data, members: [...data.members, member] });
  }, [setData, data]);

  const editMemberItem = useCallback((id: string, updatedMember: MemberItemT) => {
    const members = data.members.map(item => item.id === id ? updatedMember : item);
    setData({ ...data, members });
  }, [setData, data]);

  const removeMemberItem = useCallback((id: string)=> {
    const members = data.members.filter(item => item.id !== id);
    setData({ ...data, members });
  }, [setData, data]);



  const value = {
    data,
    setData,

    editImages,

    addProductItem,
    editProductItem,
    removeProductItem,

    addPhotoItem,
    editPhotoItem,
    removePhotoItem,

    addReviewItem,
    editReviewItem,
    removeReviewItem,

    addMemberItem,
    editMemberItem,
    removeMemberItem,
  };

  return (
    <MiniSiteContext.Provider value={value}>
      {children}
    </MiniSiteContext.Provider>
  );
};

export default MiniSiteProvider;
