import s from './styleProvider';
import React, { memo } from 'react';

const footerButtonList = [
  { label: 'Home', path: '/mini_website' },
  { label: 'Contact us', path: '/mini_website/contact_us' },
  { label: 'FAQ', path: '/mini_website/faq' },
  { label: 'Help', path: '/mini_website/help' },
];

type FooterProps = {
  background?: string
}

const Footer = memo((props: FooterProps) => {
  const { background } = props;

  const footerButtons = footerButtonList.map(item => {
    return (
      <div
        key={item.path}
        className={s.button_item}
      >
        {item.label}
      </div>
    );
  });

  return (
    <div className={s.body} style={{ background: `${background}` }}>
      <div className={s.container}>
        <h1 className={s.title}>WEBIA</h1>
        <div className={s.frame}>
          <div className={s.button_block}>
            {footerButtons}
          </div>
        </div>
      </div>
    </div>
  );
});

export { Footer };