import React, { ButtonHTMLAttributes, CSSProperties, ReactNode } from 'react';
import s from './styleProvider';

type Variant = 'primary'
  | 'outline'
  | 'outlineWhite'
  | 'action'
  | 'clear'
  | 'clearWhite'
  | 'clearBlack'
  | 'clearRed'
  | 'disabled'
  | 'idle';

interface CustomButtonProps extends ButtonHTMLAttributes<HTMLButtonElement>{
  text?: ReactNode;
  style?: CSSProperties
  variant?: Variant;
  size?: 'none' | 'm' | 'l'
  image?: React.FunctionComponent<React.SVGAttributes<SVGElement>> | null;
  imagePos?: 'start' | 'end';
  width?: 'full' | 'auto' | 'content' | 'standard';
  isEdit?: boolean
  className?: string;
}

const CustomButton: React.FC<CustomButtonProps> = (props) => {
  const {
    text,
    style,
    variant = 'primary',
    size = 'l',
    image: Image,
    imagePos,
    width = 'full',
    isEdit = false,
    onClick,
    className,
    ...otherProps
  } = props;

  const bodyStyle = `${
    s.button
  } ${
    s[variant]
  } ${
    s[size]
  } ${
    s[width]
  } ${
    isEdit && s.editButton
  } ${
    className
  }`;

  return (
    <button
      style={style}
      className={bodyStyle}
      onClick={onClick}
      {...otherProps}
    >
      {imagePos === 'start' && Image && <Image />}
      {text && <span className={s.span}>{text}</span>}
      {imagePos === 'end' && Image && <Image />}
    </button>
  );
};

export { CustomButton };
