import React from 'react';
import mst from '../miniSiteCommon/styleTemplate/styleProvider';
import { Header } from '../miniSiteCommon/header';
import { Content } from './content';
import { Footer } from '../miniSiteCommon/footer';
import { PostFooter } from '../miniSiteCommon/postfooter';
import { memo, useContext, useEffect } from 'react';
import { MiniSiteDataT } from 'templatesReact/miniSiteCommon/types';
import MiniSiteProvider from 'templatesReact/miniSiteCommon/Provider';
import { MiniSiteContext } from 'templatesReact/miniSiteCommon/Context';


const MiniSiteTemplate = memo((props: MiniSiteDataT) => {
  return (
    <MiniSiteProvider>
      <_MiniSiteTemplate {...props} />.
    </MiniSiteProvider>
  );
});

const _MiniSiteTemplate = memo((props: MiniSiteDataT) => {
  const { setData } = useContext(MiniSiteContext);

  useEffect(() => {
    setData && setData(props);
  }, [props]);

  return <main className={mst.body}>
    <Header />
    {/*<Content />*/}
    <Footer />
    <PostFooter />
  </main>;
});

export { MiniSiteTemplate };
