import React from 'react';
import s from './styleProvider';
import { CustomButton } from 'components/CustomButton/CustomButton';
import { memo, useContext, useState } from 'react';
import { HeaderNavButton } from '../headerHavButton';
import { HeaderModal } from '../headerModal';
import { MiniSiteContext } from '../Context';
import { AppRoutes } from 'pagesReact';

const headerButtonList = [
  { label: 'Home', path: AppRoutes.MINI_SITE, onlyPremium: false },
  { label: 'Reviews', path: AppRoutes.MINI_SITE_REVIEWS, onlyPremium: true },
  { label: 'Shop', path: AppRoutes.MINI_SITE_SHOP, onlyPremium: true },
  { label: 'Gallery', path: AppRoutes.MINI_SITE_GALLERY, onlyPremium: true },
  { label: 'Team', path: AppRoutes.MINI_SITE_TEAM, onlyPremium: true },
  { label: 'Contacts', path: AppRoutes.MINI_SITE_CONTACT_US, onlyPremium: false }
];

const Header = memo(() => {

  const { data } = useContext(MiniSiteContext);

  const [isOpen, setIsOpen] = useState(false);

  const onCloseModal = () => setIsOpen(false);

  const headerButtons = headerButtonList.map(item => <HeaderNavButton
    key={item.label}
    label={item.label}
    path={item.path}
    isDisabled={item.onlyPremium && !data.isPremium}
  />);

  return (
    <>
      <div className={s.body}>
        {data.isEdit && <div className={s.editContainer}>
          <span className={s.editText}>Edit Mode</span>
        </div>}
        <div className={s.container}>
          <div className={s.frame}>
            <div className={s.imageContainer}>
              <img className={s.image} src={data.logoUrl} alt='logo'/>
            </div>
            <h1 className={s.title}>{'fullName'}</h1>
          </div>
          <div className={s.frame}>
            <div className={s.button_block}>
              {headerButtons}
            </div>
            {!data.isPremium && <CustomButton
              text="Upgrade to Premium"
              variant='action'
              size='l'
              onClick={() => setIsOpen(true)}
            />}
          </div>
        </div>
      </div>

      <HeaderModal
        isOpen={isOpen}
        onClose={onCloseModal}
        onEscape={onCloseModal}
        onOverlay={onCloseModal}
      />
    </>
  );
});

export { Header };