import React from 'react';
import { memo, MouseEvent } from 'react';
import s from './styleProvider';
import Link from './linkProvider';
import { Tooltip } from 'components/tooltip';
import { useLocation } from 'react-router-dom';

type PropsT = {
  label: string
  path: string
  isDisabled?: boolean
}

const HeaderNavButton = memo((props: PropsT) => {

  const {
    label,
    path,
    isDisabled = false
  } = props;

  //const location = useLocation();

  const bodyStyle = `${
    s.body
  } ${
    isDisabled && s.disabled
  } ${
    s.active //path === location.pathname && s.active
  }`;

  const customClick = (e: MouseEvent<HTMLAnchorElement>) => {
    isDisabled && e.preventDefault();
  };

  return (
    <Tooltip text={isDisabled ? 'Only for premium users' : undefined}>
      <Link
        className={bodyStyle}
        to={path}
        onClick={customClick}
      >
        {label}
      </Link>
    </Tooltip>
  );
});

export { HeaderNavButton };
