import React from 'react';
import '../app.scss';
import { PageProps, graphql, HeadFC } from 'gatsby';
import Seo from '../gatsby/seo';
import { MiniSiteTemplate } from 'templatesReact/miniSiteTemplate';
import { dataMini } from 'pagesReact/miniSitePage/MiniSitePage';

type DataProps = {
  site: {
    buildTime: string
  }
}

const UsingTypescript: React.FC<PageProps<DataProps>> = ({
  data,
  location,
}) => {

  return(
    <div className={'mainContainer'}>
      <MiniSiteTemplate
        {...dataMini}
      />
    </div>
  );};

export const Head: HeadFC<DataProps> = () => <Seo title="Using TypeScript 1" />;

export default UsingTypescript;

export const query = graphql`
  {
    site {
      buildTime(formatString: "YYYY-MM-DD hh:mm a z")
    }
  }
`;
